import * as types from './ActionTypes';
import axios from 'axios';

export function increment() {
    return {
        type: types.INCREMENT
    };
}

export function incrementRequest(number) {
    return (dispatch) => {
        console.log(number);

        return axios.get('/counter', { number })
                    .then((response) => {
                        console.log(response);
                        dispatch(increment());  
                        console.log('success');
                    }).catch((error) => {
                        console.log('error');
                    })
    }
}

export function decrement() {
    return {
        type: types.DECREMENT
    };
}

export function decrementRequest(number) {
    return (dispatch) => {
        console.log(number);

        return axios.get('/counter', { number })
                    .then((response) => {
                        console.log(response);
                        dispatch(decrement());
                        console.log('success');
                    }).catch((error) => {
                        console.log('error');
                    })
    }
}


